"use client";  // ← This line tells Next.js this is a Client Component


import { useState } from "react";
import Link from "next/link";

export default function Navbar() {
  const [isOpen, setIsOpen] = useState(false);

  const toggleMenu = () => setIsOpen(!isOpen);

  return (
    <nav className="sticky top-2 mx-auto w-4/5 flex items-center justify-between 
                    px-8 py-2 bg-[#4241427d] backdrop-blur-md border border-white/20 
                    rounded-full shadow-lg hover:shadow-2xl transition-all duration-300 z-50">
      
      {/* Left: Hamburger (mobile only) */}
      <button
        className="md:hidden text-white text-3xl"
        onClick={toggleMenu}
      >
        ☰
      </button>

      {/* Center: Logo */}
      <h1 className="text-2xl font-bold text-center flex-1 md:flex-none">
        Punit
      </h1>

      {/* Right: Contact Button */}
      <div className="hidden md:flex justify-end">
        <a href="#contact" className="btn-gradient rounded-full">
          Contact Me
        </a>
      </div>

      {/* Desktop Menu */}
      <ul className="hidden md:flex gap-6 text-2xl">
        <li><Link href="/" className="nav-link">Home</Link></li>
        <li><Link href="/services" className="nav-link">Services</Link></li>
        <li><Link href="/about" className="nav-link">About</Link></li>
        <li><Link href="/Projects" className="nav-link">Project</Link></li>
        <li><Link href="/testimonials" className="nav-link">Testimonials</Link></li>
      </ul>

      {/* Mobile Menu */}
      {isOpen && (
        <div className="absolute top-full left-0 w-full bg-[#4241427d] backdrop-blur-md 
                        rounded-b-full border border-white/20 shadow-lg flex flex-col items-center py-4 md:hidden">
          <Link href="/" className="nav-link py-2" onClick={() => setIsOpen(false)}>Home</Link>
          <Link href="/services" className="nav-link py-2" onClick={() => setIsOpen(false)}>Services</Link>
          <Link href="/about" className="nav-link py-2" onClick={() => setIsOpen(false)}>About</Link>
          <Link href="/Projects" className="nav-link py-2" onClick={() => setIsOpen(false)}>Project</Link>
          <Link href="/testimonials" className="nav-link py-2" onClick={() => setIsOpen(false)}>Testimonials</Link>
          <a href="#contact" className="btn-gradient rounded-full mt-2" onClick={() => setIsOpen(false)}>Contact Me</a>
        </div>
      )}
    </nav>
  );
}
